.input{
  font-family: 'Satisfy', cursive;
    width: 17rem;
    height: 10rem;
    resize: none;
    font-size: larger;
    margin: 1% 43%;
    border: 2px solid #222;
    border-radius: 15px;
    background-color: white;
    position: relative;
    inline-size: 16rem;
    overflow-wrap: break-word;
}

.button{
  font-family: 'Satisfy', cursive;

    width: 200px;
    height: 4rem;
    resize: none;
    font-size: larger;
    margin: 2% 45%;
    /* margin-top: 10px; */
    border: none;
    border-radius: 5px;
    background-color: rgb(5, 179, 5);
    color: #fff;
    text-shadow: 1px 1px #444;
    font-size: 18px;
    cursor: pointer;
}