.greeting_card{
    inline-size: 16rem;
    overflow-wrap: break-word;
    width: 16rem;
    height: 10rem;
    resize: none;
    padding-left: 2%;
    margin: 1% 42%;
    border: 2px double rgb(18, 157, 11);
    border-radius: 15px;
    background-color: rgb(241, 67, 67);
    position: relative;
    font-size: large;
}